/**
 * Global Test Setup
 * 
 * This file runs once before all tests start.
 * It sets up the global test environment and initializes any required services.
 */

import { setupTestDatabase, setupTestRedis } from './setup';

export default async function globalSetup() {
  console.log('üöÄ Setting up global test environment...');
  
  try {
    // Setup test database
    console.log('üìä Setting up test database...');
    await setupTestDatabase();
    console.log('‚úÖ Test database setup complete');
    
    // Setup test Redis (mock)
    console.log('üî¥ Setting up test Redis (mock)...');
    await setupTestRedis();
    console.log('‚úÖ Test Redis setup complete');
    
    console.log('üéâ Global test setup complete!');
  } catch (error) {
    console.error('‚ùå Global test setup failed:', error);
    throw error;
  }
}

